/* 
    XPR21 Icons
*/

@font-face {
    font-family: 'XPRIcons';
    font-weight: normal;
    src: url('./XPRIcons.ttf');
    font-display: swap;
}

[class^="xpri-"], [class*=" xpri-"] {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: 'XPRIcons' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none; 
    line-height: 1;
  
    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
